.experienceTitle {
    padding-top: 4%;
    text-align: center;
    font-size: xx-large;
    color: #486648;
}

.workContainer {
    padding-top: 3%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}
@media screen and (max-width: 800px) {
    .workContainer{
        flex-direction: column;
    }
    .freelanceBox {
        width: 100%;
    }
    .solarisBox {
        width: 100%;
    }
}

.solarisBox{
    display: flex;
    flex-direction: column;
    align-items: center;
    outline: none;
    border: 1px solid #486648;
    border-radius: 10px;
    margin: 3%;
    flex: auto;
    max-width: 35%;
    outline: #486648;
    box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
    transition: box-shadow 0.3s ease;
    min-height: 450px;
    min-width: 280px;
}
.solarisBox:hover {
    box-shadow: 0 6px 15px rgb(0 0 0 / 0.4); /* Darker shadow on hover */
}
.freelanceBox{
    display: flex;
    flex-direction: column;
    align-items: center;
    outline: none;
    border: 1px solid #486648;
    border-radius: 10px;
    margin: 3%;
    flex: auto;
    max-width: 35%;
    outline: #486648;
    box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
    transition: box-shadow 0.3s ease;
    min-height: 450px;
    min-width: 280px;
}
.freelanceBox:hover {
    box-shadow: 0 6px 15px rgb(0 0 0 / 0.4); /* Darker shadow on hover */
}
.Employer {
    width: 100%;
    padding-top: 4%;
    color: #486648;
    text-align: center;
    font-size: x-large;
    height: 100px;
}
.employerLogoSolaris {
    width: 30%;
}
.employerLogoFreelance {
    width: 45%;
    clip-path: circle(39% at 52% 50%);
}
.position {
    padding-top: 3%;
    color: #486648;
    text-align: center;
    font-size: large;
}

.work {
    padding: 2%;
    text-align: center;
    color: #7E846B;
    font-size: small;
}